import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import textField from 'muse-ui/textField';

import Utils from '~/utils';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';
import { Watch } from 'vue-property-decorator';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class SetPriceComp extends Vue {

    @Action('showToast')
    showToast;

    @Action('getBindInfo')
    getBindInfo;

    @State(state => state.user.price)
    price: number;

    newPrice: string = null;

    @Watch('newPrice')
    watchAmout(newVal, oldVal) {
        this.checkAmout();
    }

    // changePrice(value) {
    //     const newPrice = Utils.checkPrice(value);
    //     this.newPrice = newPrice;
    //     const price = parseFloat(newPrice);
    //     if (price > 999) {
    //         this.showToast(new ToastMsg('金额不能大于999元'));
    //     }
    // }

    setPrice(price: string) {
        this.newPrice = price;
    }

    activated() {
        const price = this['$store'].state.user.price;
        this.newPrice = (price / 100).toFixed(2);
    }
    
    // async deactivated() {
    //     // 检查是否需要更新
    //     const price = (this.price / 100).toFixed(2);
    //     if (this.newPrice !== price) {
    //         // 更新价格 
    //         try {
    //             const res = await Api.updatePrice(parseFloat(this.newPrice) * 100);
    //             await this.getBindInfo();
    //         } catch (e) {
    //             this.showToast(new ToastMsg(e.message));
    //         }
    //     }
    // }

    // 监控金额更改
    checkAmout() {
        // 格式正则匹配
        this.newPrice = this.newPrice.replace(/[^\d.]/g, ''); // 清除“数字”和“.”以外的字符
        this.newPrice = this.newPrice.replace(/\.{2,}/g, '.'); // 只保留第一个. 清除多余的
        this.newPrice = this.newPrice.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
        this.newPrice = this.newPrice.replace(/^(0+)(\d+)\.(\d*).*$/, '$2.$3'); // 有小数点时，去除头部多余的 0,避免出现 000.12
        this.newPrice = this.newPrice.replace(/^\.(\d+).*$/, '0.$1'); // 首位为小数点，自动在头部添加0
        this.newPrice = this.newPrice.replace(/^(\d+)\.(\d\d).*$/, '$1.$2'); // 只能输入两个小数 /^(\-)*(\d+)\.(\d\d).*$/
        if (this.newPrice.indexOf('.') < 0 && this.newPrice !== '') { // 以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
            this.newPrice = String(parseFloat(this.newPrice));
            //  this.newPrice = this.redPacketPriceTemp.toString;
        }
        const price = parseFloat(this.newPrice);
        if (price > 100) {
            this.showToast(new ToastMsg('价格金额不能大于100元'));
        } else if (price <= 0) {
            this.showToast(new ToastMsg('请输入大于0的金额'));
        }
    }

    async updatePrice() {
         // 检查是否需要更新
        //  const price = (this.price / 100).toFixed(2);
         const priceTran = parseFloat(this.newPrice) * 100;
         if (this.price !== priceTran) {
             // 更新价格 
             try {
                 const res = await Api.updatePrice(parseFloat(this.newPrice) * 100);
                 await this.getBindInfo();
                 this.showToast(new ToastMsg('价格已更改'));
             } catch (e) {
                 this.showToast(new ToastMsg(e.message));
             }
         } else {
            this.showToast(new ToastMsg('价格没有更改，无须保存'));
         }
    }
}



